/* My colors */
:root{
    --primary-orange-color: #ff652f;
    --primary-yellow-color: #ffe400;
    --primary-green-color: #14a76c;
    --primary-grey-color: #747474; 
}

body{
    background : linear-gradient(to right bottom,#232526,#414345);
    color: var(--primary-green-color);
}
.container{
    margin: 0 auto;
    max-width: 920px;
    /* text-align: center; */
}
.App{
    /* margin: 2rem auto; */
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-content: center;
}
.App h1{
    font-size: 2.4rem;
    margin: 4rem auto 3rem auto;
}
.search-box{
    margin:1rem auto;
    text-align: center;
}
.search-box input{
    font-size: 1.2rem;
    text-align: center;
    height: 2.4rem;
    background-color: #ddd;
    border: none;
    border-radius: 4px;
    box-shadow: .4rem .4rem 8px black;
    transition: box-shadow .2s ease-in;
    position: relative;
    z-index: 1;
}
.search-box input:focus{
    box-shadow: none;
    /* transition: box-shadow .5s ease-in; */
}
.btn{
    /* margin: 0 0 -2rem; */
    height: 2.8rem;
    color:var(--primary-green-color);
    /* background-color: var(--primary-orange-color); */
    border:none;
    padding: 1rem;
    /* line-height: -.5rem; */
    box-shadow: .1rem .1rem 8px black;
    z-index: 4;
    position: relative;

}
.btn:hover{
    box-shadow: .2rem .2rem 18px black;
    cursor: pointer;

}

.image-box{
    flex-grow: 1;
    margin: 1rem auto;
    text-align: center;
    width: 400px;
    
}
img{
    display: none;
    width: 400px;
    height: auto;
    max-height: 500px;
}
.image-container{
    width: fit-content;
    position: relative;
}
.bounding-box{
    position: absolute;
    display: flex;
    box-shadow: 0 0 0 3px #149df2 inset;
    justify-content: center;
}
/* Styling Nav BAr */
.nav-bar{
    display: flex;
    margin-top: 2rem;
    align-content: center;
    justify-content: center;
    flex-grow: 1;
}
.nav-bar ul{
    list-style: none;

}
.nav-bar ul li{
    display: inline-block;
    padding: .5rem;

    font-size: 2rem;
}
.nav-bar ul li{
    margin-left: 2rem;
    cursor: pointer;
    border-radius: 4px;
    padding: .5rem 1.4rem;
    background-color: white;
    box-shadow: .1rem .1rem 20px black;
    /* background-color:#ddd; */
    transition: all .2s ;
    
}
.nav-bar ul li:active{
    transform: translate(2px,2px);
    box-shadow: none;
}
.nav-bar ul li:last-child{
    color: black;
    position: relative;
    cursor: not-allowed;
    background-color: var(--primary-grey-color);
    box-shadow: none;
    transform: none;
}
.nav-bar ul li:last-child::after{
    position: absolute;
    font-size: 1rem;
    text-align: center;
    content: 'Comming Soon!';
    bottom: -35%;
    left: 0;
    display: none;
    width: 100%;
    height: 40%;
    color: var(--primary-yellow-color);
    background-color: var(--primary-grey-color);
    border-radius: 3px;
}
.nav-bar ul li:last-child:hover::after{
    display: inline-block;
}
.about-page{
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* align-content: center; */
    align-items: center;
    margin-top: 2rem;
}
.about-page h1{
    font-size: 2rem;

}
.about-page p{
    /* display: inline; */
    /* text-align: start; */
    padding: 2rem;
    /* background-color: var(--primary-orange-color); */
    color: var(--primary-yellow-color);
    font-size: 1.5rem;
}
.about-page h3{
    font-size: 1.6rem;
}